/* General styles */
.container {
    max-width: 1000px;
    margin: 0 auto;
  }
  
  #popUp {
    opacity: 0.95;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background:  #fff;
  }
  
  #popUp .closePopUp {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 40px;
    color: #FE052C;
    text-decoration: none;
  }
  
  #popUp.loader {
    background-image: url(../images/ajax_loader.gif);
    background-repeat: no-repeat;
    background-position: center center;
  }
  
  /* #popUp.loader .container {
    display: none;
  } */
  
  #popUp .container {
    margin-top: 80px;
  }
  
  #popUp h1 {
    margin-bottom: 0;
  }
  
  #popUp .popUpAction {
    padding: 10px;
    background-color: #FE052C;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transform: 0.2s all;
    margin-top: 30px;
    display: inline-block;
  }
  
  #popUp .popUpAction:hover {
    color: #FE052C;
    background: #fff;
  }
  
  /* Header styles */
  header {
    background-color: #FE052C;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  }
  
  header h1 {
    color: #FFF;
    padding: 10px 2%;
    margin: 0;
    width: 16%;
    float: left;
    padding-left: 0;
  }
  
  /* Header navigation bar */
  header nav {
    padding: 10px 2%;
    width: 76%;
    float: right;
    text-align: right;
    line-height: 30px;
    margin-top: 0;
    position: relative;
    vertical-align: middle;
  }
  
  /* Search bar */
  header #search {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 20px;
    top: 10px;
  }
  
  header #search a {
    display: block;
    width: 100%;
    padding: 10px;
  }
  
  header #search a:hover {
    background: #CC0020;
  }
  
  header #search img {
    width: 100%;
    display: block;
  }
  
  header #search input {
    position: absolute;
    right: 20px;
    /*top: 10px;*/
    padding: 8px 0;
    width: 0;
    transition: 0.2s all;
    visibility: hidden;
  }
  
  header #search.active input {
    width: 160px;
    padding: 8px 10px;
    visibility: visible;
  }
  
  header #search.active a {
    background: #CC0020;
  }
  
  header nav a {
    display:block;
      padding:0 10px;
      color:#FFF;
      font-size:16px;
      line-height: 40px;
      text-decoration:none;
    transition: 0.2s all;
  }
  
  header nav a:hover {
    background-color: #fff;
    color: #CC0020;
  }
  
  nav ul {
      padding:0;
      margin:0;
    margin-right: 30px;
      list-style: none;
      position: relative;
      }
  
  nav ul li {
      display:inline-block;
      background-color: #CC0020;
    position: relative;
  }
  
  nav ul ul {
      display: none;
      position: absolute;
      top: 40px;
    right: -30px;
  }
  
  /* Display Dropdowns on Hover */
  nav ul li:hover > ul {
      display:inherit;
  }
  
  /* First Tier Dropdown */
  nav ul ul li {
      width:170px;
      float:none;
      display:list-item;
      position: relative;
  }
  
  /* Main Section */
  #main {
    margin-top: 65px;
  }
  
  /* Individual Article Styling */
  .article {
    border-bottom: 1px solid #ccc;
    transition: 0.2s all;
  }
  
  .article:hover {
    padding: 0 2%;
    background-color: #eee;
  }
  
  .article .featuredImage {
    width: 12%;
    float: left;
    margin: 1.5%;
    max-width: 60px;
  }
  
  .article .featuredImage img {
    max-width: 100%;
    border-radius: 50px;
  }
  
  .article .articleContent {
    width: 70%;
    float: left;
  }
  
  .article .articleContent a {
    text-decoration: none;
  }
  
  .article .articleContent h3 {
    margin-bottom: 0;
    color: #FE052C;
  }
  
  .article .articleContent h6 {
    margin-top: 0;
    color: #666;
  }
  
  .article .impressions {
    width: 15%;
    float: right;
    font-size: 30px;
    margin-top: 15px;
    text-align: right;
    color: #097FB2;
  }
  